"use client";

import React from "react";
import {
  MapPin,
  Briefcase,
  IndianRupee,
  Users,
  Star,
  Building2,
} from "lucide-react";

interface JobDescFullProps {
  id: string;
  title: string;
  companyName: string;
  location: string;
  description: string;
  experience: string;
  salary: string;
  openings: number;
  keySkills: string[];
}

export default function JobDesc({
  id,
  title,
  companyName,
  location,
  description,
  experience,
  salary,
  openings,
  keySkills,
}: JobDescFullProps) {
  return (
    <div className="bg-white border border-gray-200 rounded-2xl p-6 shadow-md hover:shadow-xl transition-all duration-300 cursor-pointer">
      <div className="flex items-center justify-between mb-3">
        <h2 className="text-xl font-semibold text-gray-900">{title}</h2>
        <span className="bg-blue-100 text-blue-700 text-xs px-3 py-1 rounded-full">
          {openings} Openings
        </span>
      </div>

      <p className="flex items-center gap-2 text-blue-700 text-sm font-medium mb-2">
        <Building2 size={16} /> {companyName}
      </p>

      <div className="grid sm:grid-cols-3 gap-2 text-sm text-gray-700 mb-4">
        <div className="flex items-center gap-2">
          <MapPin size={14} className="text-gray-500" /> {location}
        </div>
        <div className="flex items-center gap-2">
          <Briefcase size={14} className="text-gray-500" /> {experience}
        </div>
        <div className="flex items-center gap-2">
          <IndianRupee size={14} className="text-gray-500" /> {salary}
        </div>
      </div>

      <p className="text-gray-600 text-sm mb-4 line-clamp-2">{description}</p>

      <div className="flex flex-wrap gap-2">
        {keySkills.slice(0, 3).map((skill, i) => (
          <span
            key={i}
            className="bg-blue-50 text-blue-700 text-xs font-medium px-3 py-1 rounded-full border border-blue-200"
          >
            {skill}
          </span>
        ))}
        {keySkills.length > 3 && (
          <span className="text-xs text-gray-500">
            +{keySkills.length - 3} more
          </span>
        )}
      </div>
    </div>
  );
}
