"use client";

import React, { useState } from "react";
import { useUsers, useDeleteUser, useUpdateUser } from "@/app/customHooks/useUser";

import { X, Phone, Mail, UserRound, Shield } from "lucide-react";
import { UserI } from "@/app/utility/props";

export default function UsersList() {
  const { data: users, isLoading, error } = useUsers();
  const { mutate: deleteUser } = useDeleteUser();
  const { mutate: updateUser } = useUpdateUser();

  const [editingUser, setEditingUser] = useState<UserI| null>(null);
  const [formData, setFormData] = useState({
    firstName: "",
    lastName: "",
    phone: "",
    role: "user",
  });

  const handleEditClick = (user: UserI) => {
    setEditingUser(user);
    setFormData({
      firstName: user.firstName,
      lastName: user.lastName,
      phone: user.phone,
      role: user.role,
    });
  };

  const handleSave = () => {
    if (!editingUser) return;
    updateUser({ id: editingUser.id, data: formData });
    setEditingUser(null);
  };

  if (isLoading) return <p className="text-center py-6">Loading users...</p>;
  if (error) return <p className="text-center text-red-500 py-6">Failed to load users.</p>;

  return (
    <div className="p-6 bg-gray-50 min-h-screen">
      <h1 className="text-2xl font-semibold text-gray-800 mb-6">Manage Users</h1>

      <div className="grid sm:grid-cols-2 lg:grid-cols-3 gap-6">
        {users?.map((user: UserI) => (
          <div
            key={user.id}
            className="bg-white rounded-2xl shadow-md hover:shadow-lg transition-all p-5 border border-gray-100 flex flex-col justify-between"
          >
            <div>
              <div className="flex items-center gap-3 mb-3">
                <div className="bg-blue-100 text-blue-700 rounded-full p-2">
                  <UserRound size={22} />
                </div>
                <div>
                  <h2 className="text-lg font-semibold text-gray-800">
                    {user.firstName} {user.lastName}
                  </h2>
                  <p className="text-sm text-gray-500">{user.role.toUpperCase()}</p>
                </div>
              </div>

              <div className="text-sm text-gray-700 space-y-2">
                <div className="flex items-center gap-2">
                  <Mail size={16} className="text-gray-500" />
                  <span>{user.email}</span>
                </div>
                <div className="flex items-center gap-2">
                  <Phone size={16} className="text-gray-500" />
                  <span>{user.phone || "N/A"}</span>
                </div>
              </div>
            </div>

            <div className="flex justify-between items-center mt-5">
              <button
                onClick={() => handleEditClick(user)}
                className="text-sm bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700 transition-all"
              >
                Edit
              </button>
              <button
                onClick={() => deleteUser(user.id)}
                className="text-sm text-red-600 border border-red-500 px-4 py-2 rounded-lg hover:bg-red-50 transition-all"
              >
                Delete
              </button>
            </div>
          </div>
        ))}
      </div>

      {/* Edit User Modal */}
      {editingUser && (
        <div className="fixed inset-0 bg-black/40 flex justify-center items-center z-50">
          <div className="bg-white rounded-2xl shadow-2xl p-6 w-[90%] sm:w-[400px] relative">
            <button
              onClick={() => setEditingUser(null)}
              className="absolute top-3 right-3 text-gray-500 hover:text-gray-700"
            >
              <X size={22} />
            </button>

            <h3 className="text-lg font-semibold mb-4 text-gray-800">
              Edit User
            </h3>

            <div className="flex flex-col gap-3">
              <input
                type="text"
                value={formData.firstName}
                onChange={(e) => setFormData({ ...formData, firstName: e.target.value })}
                placeholder="First Name"
                className="border p-2 rounded-md focus:outline-blue-500"
              />
              <input
                type="text"
                value={formData.lastName}
                onChange={(e) => setFormData({ ...formData, lastName: e.target.value })}
                placeholder="Last Name"
                className="border p-2 rounded-md focus:outline-blue-500"
              />
              <input
                type="text"
                value={formData.phone}
                onChange={(e) => setFormData({ ...formData, phone: e.target.value })}
                placeholder="Phone"
                className="border p-2 rounded-md focus:outline-blue-500"
              />
              <select
                value={formData.role}
                onChange={(e) => setFormData({ ...formData, role: e.target.value })}
                className="border p-2 rounded-md focus:outline-blue-500"
              >
                <option value="user">User</option>
                <option value="admin">Admin</option>
              </select>
            </div>

            <div className="flex justify-end gap-3 mt-6">
              <button
                onClick={() => setEditingUser(null)}
                className="px-4 py-2 border rounded-md hover:bg-gray-100 transition-all"
              >
                Cancel
              </button>
              <button
                onClick={handleSave}
                className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition-all"
              >
                Save
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
