"use client";

import React, { useState } from "react";
import { useDeleteJob, useJobsforAdmin } from "@/app/customHooks/useJobs";
import { JobWithApplications } from "@/app/utility/props";
import JobForm from "./JobForm";
import ApplicationTable from "./ApplicationTable";
import { X } from "lucide-react";

export default function JobsList() {
  const { data, isLoading, error } = useJobsforAdmin();
  const jobs = data as JobWithApplications[];
  const { mutate: deleteJob } = useDeleteJob();

  const [showForm, setShowForm] = useState(false);
  const [selectedJob, setSelectedJob] = useState<JobWithApplications | null>(null);

  if (isLoading) return <p className="text-center py-6">Loading jobs...</p>;
  if (error) return <p className="text-center text-red-500 py-6">Failed to load jobs.</p>;

  return (
    <div className="p-6 bg-gray-50 min-h-screen">
      <div className="flex justify-between items-center mb-6">
        <h1 className="text-2xl font-semibold text-gray-800">Manage Jobs</h1>
        <button
          onClick={() => setShowForm(true)}
          className="bg-blue-600 hover:bg-blue-700 text-white px-5 py-2 rounded-lg shadow transition-all"
        >
          + Add Job
        </button>
      </div>

      {showForm && <JobForm onClose={() => setShowForm(false)} />}

      <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6">
        {jobs?.map((job) => (
          <div
            key={job._id}
            className="bg-white rounded-2xl shadow-md p-5 border border-gray-100 hover:shadow-lg transition-all flex flex-col justify-between"
          >
            <div>
              <h2 className="text-lg font-semibold text-gray-800 mb-1">{job.title}</h2>
              <p className="text-gray-600 text-sm mb-2">{job.companyName}</p>
              <p className="text-gray-500 text-sm mb-4">{job.location}</p>
              <p className="text-gray-700 text-sm mb-3 line-clamp-3">
                {job.description}
              </p>

              <div className="flex flex-wrap gap-2 mb-3">
                {job.keySkills?.map((skill, i) => (
                  <span
                    key={i}
                    className="bg-blue-50 text-blue-700 text-xs font-medium px-2 py-1 rounded-md"
                  >
                    {skill}
                  </span>
                ))}
              </div>

              <div className="flex justify-between text-sm text-gray-600">
                <span>ðŸ’¼ {job.experience || "N/A"}</span>
                <span>ðŸ’° {job.salary || "N/A"}</span>
              </div>
            </div>

            <div className="flex justify-between items-center mt-5">
              <button
                onClick={() => setSelectedJob(job)}
                className="text-sm bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700 transition-all"
              >
                View Applicants
              </button>
              <button
                onClick={() => deleteJob(job._id)}
                className="text-sm text-red-600 border border-red-500 px-4 py-2 rounded-lg hover:bg-red-50 transition-all"
              >
                Delete
              </button>
            </div>
          </div>
        ))}
      </div>

      {/* Applicants Modal */}
      {selectedJob && (
        <div className="fixed inset-0 bg-black/50 flex justify-center items-center z-50">
          <div className="bg-white rounded-2xl shadow-2xl p-6 w-[90%] md:w-[70%] lg:w-[60%] max-h-[80vh] overflow-y-auto relative">
            <button
              onClick={() => setSelectedJob(null)}
              className="absolute top-4 right-4 text-gray-500 hover:text-gray-700"
            >
              <X size={22} />
            </button>

            <h2 className="text-xl font-semibold text-gray-800 mb-4">
              Applicants for {selectedJob.title}
            </h2>

            {selectedJob.applications && selectedJob.applications.length > 0 ? (
              <ApplicationTable
                title={selectedJob.title}
                applications={selectedJob.applications}
              />
            ) : (
              <p className="text-gray-600 text-center py-10">
                No applications yet for this job.
              </p>
            )}
          </div>
        </div>
      )}
    </div>
  );
}
